import Image from 'next/image';
import Link from 'next/link';
import { Listing } from '@/types';

interface ListingCardProps {
  listing: Listing;
}

export default function ListingCard({ listing }: ListingCardProps) {
  const roomTypeLabels = {
    single: 'Single Room',
    shared: 'Shared Room',
    entire: 'Entire Place',
  };

  const roomTypeColors = {
    single: 'bg-blue-100 text-blue-800',
    shared: 'bg-green-100 text-green-800',
    entire: 'bg-purple-100 text-purple-800',
  };

  return (
    <Link href={`/listings/${listing.id}`} className="group">
      <div className="overflow-hidden rounded-2xl bg-white shadow-sm transition-all duration-300 hover:shadow-xl">
        <div className="relative h-64 w-full overflow-hidden bg-gray-200">
          {listing.images && listing.images.length > 0 ? (
            <Image
              src={listing.images[0]}
              alt={listing.title}
              fill
              className="object-cover transition-transform duration-500 group-hover:scale-110"
              sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
              unoptimized
            />
          ) : (
            <div className="flex h-full items-center justify-center bg-gradient-to-br from-gray-100 to-gray-200">
              <svg
                className="h-16 w-16 text-gray-400"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={1.5}
                  d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z"
                />
              </svg>
            </div>
          )}
          <div className="absolute right-3 top-3">
            <span className={`inline-flex items-center rounded-full px-3 py-1 text-xs font-semibold ${roomTypeColors[listing.roomType]}`}>
              {roomTypeLabels[listing.roomType]}
            </span>
          </div>
        </div>

        <div className="p-5">
          <div className="mb-2 flex items-start justify-between gap-2">
            <div className="flex-1 min-w-0">
              <h3 className="truncate text-lg font-semibold text-gray-900 group-hover:text-rose-600 transition-colors">
                {listing.title}
              </h3>
              <p className="mt-1 text-sm text-gray-500 truncate">{listing.location}</p>
            </div>
            <div className="flex-shrink-0 text-right">
              <p className="text-xl font-bold text-gray-900">
                ${listing.price.toLocaleString()}
              </p>
              <p className="text-xs text-gray-500">per month</p>
            </div>
          </div>

          <p className="mb-4 line-clamp-2 text-sm leading-relaxed text-gray-600">
            {listing.description}
          </p>

          {listing.amenities && listing.amenities.length > 0 && (
            <div className="flex flex-wrap gap-2">
              {listing.amenities.slice(0, 4).map((amenity, index) => (
                <span
                  key={index}
                  className="inline-flex items-center rounded-lg bg-gray-100 px-2.5 py-1 text-xs font-medium text-gray-700"
                >
                  {amenity}
                </span>
              ))}
              {listing.amenities.length > 4 && (
                <span className="inline-flex items-center rounded-lg bg-gray-100 px-2.5 py-1 text-xs font-medium text-gray-700">
                  +{listing.amenities.length - 4}
                </span>
              )}
            </div>
          )}
        </div>
      </div>
    </Link>
  );
}
