'use client';

import { useState, useEffect } from 'react';
import { useSearchParams } from 'next/navigation';
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';
import SearchBar from '@/components/SearchBar';
import ListingCard from '@/components/ListingCard';
import Loading from '@/components/Loading';
import Error from '@/components/Error';
import { fetchListings } from '@/lib/api';
import { Listing } from '@/types';

export default function ListingsPage() {
  const searchParams = useSearchParams();
  const [listings, setListings] = useState<Listing[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [page, setPage] = useState(1);
  const [hasMore, setHasMore] = useState(true);

  const city = searchParams.get('city') || '';
  const minPrice = searchParams.get('minPrice') || '';
  const maxPrice = searchParams.get('maxPrice') || '';
  const roomType = searchParams.get('roomType') || '';

  useEffect(() => {
    loadListings();
  }, [searchParams]);

  const loadListings = async () => {
    setLoading(true);
    setError(null);

    try {
      const response = await fetchListings({
        city: city || undefined,
        minPrice: minPrice ? parseInt(minPrice) : undefined,
        maxPrice: maxPrice ? parseInt(maxPrice) : undefined,
        roomType: roomType || undefined,
        page: 1,
        limit: 12,
      });

      setListings(response.listings);
      setHasMore(response.meta.page < response.meta.totalPages);
      setPage(1);
    } catch (err: any) {
      setError(err.message || 'Failed to load listings');
    } finally {
      setLoading(false);
    }
  };

  const loadMore = async () => {
    if (loading || !hasMore) return;

    setLoading(true);
    try {
      const response = await fetchListings({
        city: city || undefined,
        minPrice: minPrice ? parseInt(minPrice) : undefined,
        maxPrice: maxPrice ? parseInt(maxPrice) : undefined,
        roomType: roomType || undefined,
        page: page + 1,
        limit: 12,
      });

      setListings((prev) => [...prev, ...response.listings]);
      setHasMore(response.meta.page < response.meta.totalPages);
      setPage((prev) => prev + 1);
    } catch (err: any) {
      setError(err.message || 'Failed to load more listings');
    } finally {
      setLoading(false);
    }
  };

  if (loading && listings.length === 0) {
    return (
      <div className="flex min-h-screen flex-col">
        <Navbar />
        <Loading />
        <Footer />
      </div>
    );
  }

  if (error && listings.length === 0) {
    return (
      <div className="flex min-h-screen flex-col">
        <Navbar />
        <Error message={error} onRetry={loadListings} />
        <Footer />
      </div>
    );
  }

  return (
    <div className="flex min-h-screen flex-col bg-white">
      <Navbar />

      <main className="flex-1">
        <div className="bg-gradient-to-br from-rose-50 to-white py-12">
          <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
            <div className="mb-8">
              <h1 className="text-4xl font-bold tracking-tight text-gray-900 md:text-5xl">
                Explore accommodations
              </h1>
              <p className="mt-3 text-lg text-gray-600">
                Find your perfect place to stay
              </p>
            </div>
            <SearchBar
              initialCity={city}
              initialBudget={{
                min: minPrice ? parseInt(minPrice) : 0,
                max: maxPrice ? parseInt(maxPrice) : 5000,
              }}
              initialRoomType={roomType as 'single' | 'shared' | 'entire' | undefined}
            />
          </div>
        </div>

        <div className="mx-auto max-w-7xl px-4 py-12 sm:px-6 lg:px-8">
          {error && (
            <div className="mb-6 rounded-xl bg-red-50 p-4 ring-1 ring-red-200">
              <p className="text-sm font-medium text-red-800">{error}</p>
            </div>
          )}

          {listings.length === 0 ? (
            <div className="py-20 text-center">
              <svg
                className="mx-auto h-16 w-16 text-gray-400"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={1.5}
                  d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                />
              </svg>
              <h3 className="mt-4 text-lg font-semibold text-gray-900">No listings found</h3>
              <p className="mt-2 text-gray-600">
                Try adjusting your search filters to find more results.
              </p>
            </div>
          ) : (
            <>
              <div className="mb-8">
                <p className="text-gray-600">
                  <span className="font-semibold text-gray-900">{listings.length}</span>{' '}
                  {listings.length === 1 ? 'listing' : 'listings'} found
                </p>
              </div>

              <div className="grid grid-cols-1 gap-8 sm:grid-cols-2 lg:grid-cols-3">
                {listings.map((listing) => (
                  <ListingCard key={listing.id} listing={listing} />
                ))}
              </div>

              {hasMore && (
                <div className="mt-12 text-center">
                  <button
                    onClick={loadMore}
                    disabled={loading}
                    className="inline-flex items-center gap-2 rounded-xl bg-rose-600 px-8 py-3 text-base font-semibold text-white shadow-lg shadow-rose-600/25 transition-all hover:bg-rose-700 hover:shadow-xl hover:shadow-rose-600/30 focus:outline-none focus:ring-2 focus:ring-rose-600 focus:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50"
                  >
                    {loading ? (
                      <>
                        <svg className="h-5 w-5 animate-spin" fill="none" viewBox="0 0 24 24">
                          <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                          <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                        </svg>
                        Loading...
                      </>
                    ) : (
                      'Load More'
                    )}
                  </button>
                </div>
              )}
            </>
          )}
        </div>
      </main>

      <Footer />
    </div>
  );
}
